 .background{
    background-color: #ff0000;
    } 


.volume-button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  position: relative;
  width: 30px;
  height: 30px;
  padding: 0;

  .icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 12px;
    height: 12px;
    border-style: solid;
    border-width: 2px 0 0 2px;
    border-color: #333;
    border-radius: 1px;
  }

  .volume-level {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 2px;
    background-color: #333;
    transition: height 0.2s ease-in-out;
  }
}
